<template>
      <div>
        <el-form-item label="">
         <el-checkbox-group style="width:500px;" v-model="checkItems" @change="changeCheckItems">
             <el-checkbox :label="item.name" v-for="item in filedItems" :key="item.name" style="margin-left:0; width:160px;margin-bottom:5px;">
               {{item.text}}
             </el-checkbox>
           </el-checkbox-group>
           </el-form-item>
      <div  class="canvas-warp">
         <div  class="canvas-tool">
            <cms-picture-zone v-model="backgroundImgId" @change="getImg"  :preview="false">
              <cms-button type='info' size='small'></cms-button>
            </cms-picture-zone>
           </div>
         <div :style="canvasStyles" class="canvas-inner" id="canvasBody">
            <canvas id="tplCanvas" width="870" height="480"></canvas>
            <template v-for="(item,index) in canvasItems">
            <vue-drag-resize :isActive="true"
            :parentLimitation='true'
            v-model="canvasItems[index]"
            :w="item.width" :h="item.height"
            :x="item.left"
            :y="item.top"
            :minh="20"
            :key="item.id"
            >{{item.text}}</vue-drag-resize>
            </template>
           </div>
           </div>
      </div>
</template>
<script>
 import vueDragResize from './vue-drag-resize.vue'
export default {
   data() {
     return {
       backgroundImgId: '',
       filedItems: [
         {
           name: 'orederId',
           text: ''
         }, {
           name: 'senderName',
           text: ''
         },
         {
           name: 'senderTel',
           text: ''
         }, {
           name: 'senderCompany',
           text: ''
         }, {
           name: 'senderAddress',
           text: ''
         }, {
           name: 'senderCode',
           text: ''
         }, {
           name: 'desc',
           text: ''
         }, {
           name: 'receiverName',
           text: ''
         }, {
           name: 'receiverTel',
           text: ''
         }, {
           name: 'receiverAddress',
           text: ''
         }, {
           name: 'receiverCode',
           text: ''
         }
       ],
       canvasWidth: 200,
       canvasHeight: 150,
       checkItems: [],
       canvasItems: [], // 
       imageObject: null,
       isEdit: false// 
     }
   },
   components: {
     vueDragResize
   },
   props: {
     width: {
       type: [Number, String],
       default: 200
     },
     height: {
       type: [Number, String],
       default: 150
     },
     imgData: {
       type: Object,
       default: function() {
         return null
       }
     },
     filedData: {
       type: Array,
       default: function() {
         return []
       }
     }
   },
   watch: {
     checkItems: {
       handler(newVal, oldVal) {
         const key = this.getArrDifference(oldVal, newVal)[0]
         const obj = this.filedItems.filter((v) => { return v.name === key })[0]
         const index = this.canvasItems.findIndex(v => { return v.id === key })
         if (index > -1) {
           this.canvasItems.splice(index, 1)
         } else {
           if (this.isEdit) {
             this.canvasItems = Object.assign([], this.filedData)
             this.isEdit = false
           } else {
             const canvasObj = {
               id: obj.name,
               text: obj.text,
               left: 0,
               top: 0,
               width: 120,
               height: 20
             }
             this.canvasItems.push(canvasObj)
           }
         }
       }
     },
     canvasItems: {
       handler() {
         this.$emit('change', this.canvasItems, this.backgroundImgId)
       },
       deep: true
     },
     width() {
       this.resizeCanvas(this.width, this.height)
     },
     height() {
       this.resizeCanvas(this.width, this.height)
     },
     imgData: {
       handler() {
         if (this.imgData && this.imgData.id !== '') {
           this.backgroundImgId = this.imgData.id
           this.getImg([this.imgData])// 
         } else {
           this.backgroundImgId = ''
         }
       },
       immediate: true

     },
     filedData: {
       handler() {
         if (this.filedData.length > 0) {
           this.isEdit = true
           const checkItems = this.filedData.map(v => v.id)
           this.checkItems = Object.assign([], checkItems)
         }
       },
       immediate: true
     }
   },
   computed: {
     canvasStyles() {
       return {
         width: this.width + 'mm',
         height: this.height + 'mm'
       }
     }
   },
   methods: {
     resizeCanvas(w, h) {
       this.$nextTick(() => {
         const body = document.getElementById('canvasBody')
         const cvs = document.getElementById('tplCanvas')
         cvs.width = body.clientWidth
         cvs.height = body.clientHeight
         var ctx = cvs.getContext('2d')
         if (this.imageObject) {
           ctx.drawImage(this.imageObject, 0, 0, cvs.width, cvs.height)// thisimgObj,470*480
         }
       })
     },
     changeCheckItems(data) {
       this.isEdit = false
     },
     getImg(data) {
       if (data.length > 0) {
         this.backgroundImgId = data[0].id
       }
       this.$nextTick(() => {
         this.imageObject = new Image()
         this.imageObject.src = this.$cms.filterUrl(data[0].url)
         this.imageObject.onload = () => {
           this.resizeCanvas(this.width, this.height)
         }
       })
       this.$emit('change', this.canvasItems, this.backgroundImgId)
     },
     getArrDifference(arr1, arr2) { // 
       return arr1.concat(arr2).filter(function(v, i, arr) {
         return arr.indexOf(v) === arr.lastIndexOf(v)
       })
     }
   }
}
</script>

 <style lang="scss" scoped>
.canvas-warp{
   width: 100%;
   min-height: 400px;
   border: 1px solid #eee;
}
.canvas-tool{
  width: 100%;
  padding: 10px 10px 0 10px;
  border-bottom: 1px solid #eee;
}
.canvas-inner{
  position: relative;
  margin: 0 auto;
  overflow: hidden;
  // background-size: 100% 100%;
  background-color: #fff;
  border: 1px solid #666
}
#tplCanvas{
     height: inherit;
       background: -webkit-linear-gradient(top, transparent 10px, #666 10px),-webkit-linear-gradient(left, transparent 10px, #666 10px);
        background-size: 11px 11px;
}

</style>






