<template>
  <div v-loading="loading">
    <cms-breadcrumb to="/waybillTemplate/list" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse="true">
        <li> * </li>
        <li></li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px">
        <el-form-item label="">
          <el-select v-model="templateSelect" placeholder="" @change="getTemplateSelect">
            <el-option
              v-for="item in templateList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="name" :error="errorMessage.name">
          <el-input v-model="formData.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="logisticsCompanyId" ref="logisticsCompanyId">
          <el-select v-model="formData.logisticsCompanyId" placeholder="">
            <el-option
              v-for="item in logItems"
              :key="item.id"
              :label="item.cnName"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="board-width" label="" style="float:left" prop="width" :error='errorMessage.width'>
          <el-input maxlength="5" v-model="formData.width" placeholder="" style="width:185px" >
            <template slot="append">mm</template>
          </el-input>*
        </el-form-item>
        <el-form-item
          class="board-height"
          style="float:left;margin-left:-210px"
          prop="height"
          :error="errorMessage.height"
        >
          <el-input maxlength="5" v-model="formData.height" placeholder="" style="width:185px">
            <template slot="append">mm</template>
          </el-input>
        </el-form-item>
        <div style="clear:both"></div>
        <el-form-item label="" prop="upperOffset">
          <el-input v-model="formData.upperOffset" placeholder="" maxlength="3">
            <template slot="append">mm</template>
          </el-input>
          <div class="gray"></div>
        </el-form-item>
        <el-form-item label="" prop="leftOffset">
          <el-input v-model="formData.leftOffset" placeholder="" maxlength="3">
            <template slot="append">mm</template>
          </el-input>
          <div class="gray"></div>
        </el-form-item>
        <el-form-item label="" prop="isEnable" :error="errorMessage.isEnable">
          <cms-switch v-model="formData.isEnable"></cms-switch>
          <div class="gray"></div>
        </el-form-item>
        <el-form-item label="" prop="isDefault" ref="isDefault">
          <cms-switch v-model="formData.isDefault"></cms-switch>
        </el-form-item>
        <tpl @change="getTplData" :width="formData.width" :height="formData.height"
        :filedData="tplField"
         :imgData="formData.bgImgResourceData" v-if="status"></tpl>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="save(false)" v-color></cms-button>
        <cms-button @click="save(true)" v-color></cms-button>
        <cms-button type="rest" @click="rest"></cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { waybillTemplateSave, waybillTemplateList } from '@/api/setting/waybillTemplate.js'
import { logisticsCompanyList } from '@/api/setting/logisticsCompany.js'
import tpl from './components/tpl'
var defaultData = {
  name: '',
  waybillType: 2,
  width: 200,
  height: 150,
  upperOffset: '',
  leftOffset: '',
  isEnable: true,
  logisticsCompanyId: '',
  backgroundImgId: '',
  isDefault: false
}
export default {
  name: 'waybillTemplateSave',
  mixins: [form],
  components: {
    tpl
  },
  data() {
    var validateNum = (rule, value, callback) => {
      const reg = /^[0-9]\d*$/
      if (value === '' || value === null) {
        callback()
      } else if (!reg.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      templateSelect: null,
      logItems: [], // 
      templateList: [], // 
      selTplList: [],
      formData: Object.assign({}, defaultData),
      saveRules: {
        // 
        name: [rules.required(''), rules.len(1, 20)],
        width: [rules.required(''), rules.number()],
        height: [rules.required(''), rules.number()],
        upperOffset: [rules.custom(validateNum)],
        leftOffset: [rules.custom(validateNum)],
        logisticsCompanyId: [rules.required('', 'change')],
        isEnable: [rules.required('')]
      },
      tplField: []
    }
  },
  methods: {
    save(state) {
      // 
      const params = Object.assign({}, this.formData)
      params.tplField = JSON.stringify(params.tplField)
      delete params.bgImgResourceData
      delete params.id
      this.saveForm(
        waybillTemplateSave,
        params,
        state,
        '/waybillTemplate/list',
        'list'
      )
    },
    getlogItem() {
      // 
      logisticsCompanyList({ page: 1, size: 9999 }).then(res => {
        this.logItems = res.data
      })
    },
    getTemplate() {
      waybillTemplateList({ waybillType: 1 }).then(res => {
        if (res.code === 200) {
          this.templateList = []
          res.data.forEach(v => {
            this.selTplList[v.id] = v
            if (v.isEnable) {
              this.templateList.push(v)
            }
          })
        }
      })
    },
    getTemplateSelect(val) {
      var data = this.selTplList[val]
      this.formData = Object.assign(defaultData, data)
      if (data.tplField) {
        this.tplField = Object.assign([], JSON.parse(data.tplField))
        this.status = true
      }
      this.formData.logisticsCompanyId = data.logisticsCompany.id
      this.formData.backgroundImgId = data.bgImgResourceData ? data.bgImgResourceData.id : ''
    },
    getTplData(data, imgId) {
      this.formData.tplField = data
      this.formData.backgroundImgId = imgId
    }
  },
  activated() {
    this.autoFocus()
    if (this.$route.query.type === 'save') {
      this.formData = Object.assign({}, defaultData)
    }
  },
  created() {
    this.getlogItem()
    this.getTemplate()
  }
}
</script>
<style>
</style>




