// 
import axios from './../fetch'

// 
export const logisticsCompanyPage = params => { return axios.get('/admin/store/logisticscompany/page', { params: params }) }

// 
export const logisticsCompanyGet = params => { return axios.get('/admin/store/logisticscompany/get', { params: params }) }
// 
export const logisticsCompanyList = params => { return axios.get('/admin/store/logisticscompany/list', { params: params }) }

// 
export const logisticsCompanySave = params => { return axios.post('/admin/store/logisticscompany/save', params) }
// 
export const logisticsCompanyEdit = params => { return axios.post('/admin/store/logisticscompany/update', params) }
// 
export const logisticsCompanyDelete = params => { return axios.post('/admin/store/logisticscompany/delete', params) }

// 
export const logisticsCompanySort = params => { return axios.post('/admin/store/logisticscompany/sort', params) }

// 
export const logisticsCompanyEnable = params => { return axios.post('/admin/store/logisticscompany/enable', params) }

// 
export const logisticsCompanyDisable = params => { return axios.post('/admin/store/logisticscompany/disable', params) }




